@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Source Sans Pro", sans-serif;
}

html {
  font-size: 62.5%;
}

.main-div {
  min-height: 100vh;
  background: #060822;
  display: flex;
  justify-content: center;
  /* align-items: center; */
}

.child-div {
  /* border: 2px solid white; */
  text-align: center;
  margin-top: 5rem;
  min-width: 45rem;
  height: 54rem;
  /* overflow-y: auto; */
}

.child-div figure img {
  width: 10rem;
  height: 8rem;
}

.child-div figure figcaption {
  color: #fff;
  font-size: 2.2rem;
  padding-top: 2rem;
  text-transform: capitalize;
}

input {
  /* display: block; */
   min-width: 43rem;
  height: 3.4rem;
  padding: 2rem 1.2rem;
  font-size: 1.8rem;
  line-height: 1.42857143;
  margin-top: 2rem;
  color: rgb(58, 57, 57);
  background-color: #fff;
  background-image: none;
  border: 0.1rem solid #ccc;
  border-radius: 0.4rem;
  -webkit-box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
  box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

input,
input:focus,
input:active,
input:active:focus {
  border: none;
  padding: 2.3rem 1.5rem;
  outline: none;
}
input {
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
input:focus {
  -webkit-box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
}



.todo-btn {
  width: 5rem;
  display: flex;
  justify-content: space-around;
}

.fa {
  margin-left: -2.5rem;
  cursor: pointer;
  pointer-events: auto;
  z-index: 10;
  background-color: #fff;
  background-image: none;
  color: #666;
  -webkit-transition: color 0.15s linear;
  transition: color 0.15s linear;
  font-size: 2rem;
}
.fa:hover {
  color: rgb(47, 214, 122);
}

.fa-plus:before {
  content: "\f067";
  font-size: 1.5rem;
}

.showItems {
  margin-top: 3rem;
  text-align: center;
  /* border: 2px solid white; */
  overflow-y: auto;
height: 30rem;
}

.eachItem {
  background: #fff;
  padding: 1.2rem 0rem;
  border-radius: 0.5rem;
  margin-bottom: 0.7rem;
  margin-left: 2.5rem;
  margin-right: 1rem;
  width: 50.5rem;
  word-break: break-word;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* color: #000; */
}

.eachItem:hover {
  background: rgb(85, 41, 220);

}

.showItems .eachItem h3 {
  padding-left: 1rem;
  font-size: 1.8rem;
  color: #000;
}


.fa-trash-alt,
.fa-edit {
  /* margin-left: -2.5rem; */
  cursor: pointer;
  pointer-events: auto;
  z-index: 10;
  /* color: rgb(251, 251, 251); */
  color: red;
  -webkit-transition: color 0.15s linear;
  transition: color 0.15s linear;
  font-size: 2rem;
}


 

.fa-edit {
  margin-left: -3.5rem;
  color: green;
  /* background-color: #fff; */

}
.eachItem:hover .fa-edit {
  color: rgb(47, 214, 122);
  background-color: rgb(85, 41, 220);


}

.eachItem:hover .fa-trash-alt {
  color: rgb(236, 0, 20);
  background-color: rgb(85, 41, 220);
}



.eachItem:hover h3 {
  color: #fff;
}

